import { Routes, Route, Navigate } from 'react-router-dom';
import { ProtectedRoute, UnprotectedRoute, ExceptionRoute } from './components/protectedroute';
import { selectLoggedIn } from './redux/authReducer';
import { getUserData, selectUserRole, setGettingUserData, selectUser } from './redux/userReducer';
import { syncCoinAllow } from './redux/settingReducer';
import { useDispatch, useSelector } from 'react-redux';
import React, { useEffect } from 'react';
import { Suspense } from "react";


// Font Awesome Style Sheet
import "@fortawesome/fontawesome-free/css/all.min.css";
import LoadingSpinner from './components/LoadingSpinner';

const  Home = React.lazy(() => import('./pages/home'));
const  Authenticate = React.lazy(() => import('./pages/authenticate'));
const  DashboardCustomer = React.lazy(() => import('./pages/dashboard/customer'));
const  DashboardMerchant = React.lazy(() => import('./pages/dashboard/merchant'));
const  DashboradAuthenticator = React.lazy(() => import('./pages/dashboard/authenticator'));
const  VerifyUser = React.lazy(() => import('./pages/auth/verify'));
const  Logout = React.lazy(() => import('./pages/auth/logout'));
const  POS = React.lazy(() => import('./pages/dashboard/merchant_group/pos'));
const  Details = React.lazy(() => import('./pages/dashboard/merchant_group/details'));
const  Report = React.lazy(() => import('./pages/dashboard/merchant_group/report'));
const  Carts = React.lazy(() => import('./pages/dashboard/merchant_group/savedcarts'));
const  Inventory = React.lazy(() => import('./pages/dashboard/merchant_group/inventory'));
const  Settings = React.lazy(() => import('./pages/dashboard/merchant_group/settings'));
const  Orders = React.lazy(() => import('./pages/dashboard/merchant_group/orders'));
const  Search = React.lazy(() => import('./pages/dashboard/search'));
const  Wallet = React.lazy(() => import('./pages/dashboard/wallet'));

const AppLayout = () => {
  const isLoggedIn = useSelector(selectLoggedIn);
  const userRole = useSelector(selectUserRole);
  const user = useSelector(selectUser);
  const dispatch = useDispatch();

  useEffect(() => {
    if (isLoggedIn) {
      try {
        dispatch(getUserData(JSON.parse(localStorage.user).user.id));
      } catch (error) {
        console.error(error)
      }
    } else {
      dispatch(setGettingUserData(false));
    }
  }, [isLoggedIn, dispatch]);

  useEffect(() => { 
    if (user) {   
      dispatch(syncCoinAllow(user));
    }
  }, [user])

  return (
    <div className="min-h-screen bg-app-form-border bg-opacity-10">
      <Suspense fallback={<LoadingSpinner />}>
        <Routes>          
          <Route path="/auth" element={
            <Authenticate />
          } />
          <Route path="/" element={
            isLoggedIn ?
            <ProtectedRoute>
              {userRole === 'authenticator' ? <DashboradAuthenticator /> : (userRole === 'merchant' ? <DashboardMerchant /> : <DashboardCustomer />)}
            </ProtectedRoute> :
            <Navigate to="/auth" replace />
          }/>
          <Route path="/verify/:token" element={<VerifyUser />} />
          <Route path="/logout" element={<Logout />} />
          <Route path="/pos" element={<POS />} />
          <Route path="/details/:categoryId" element={<Details />} />
          <Route path="/report" element={<Report />} />
          <Route path="/carts/:name" element={<Carts />} />
          <Route path="/inventory" element={<Inventory />} />
          <Route path="/settings" element={<Settings />} />
          <Route path="/orders" element={<Orders />} />
          <Route path="/search/:filter/:keyword" element={<Search />}/>
          <Route path="/wallet" element={<Wallet />} />
        </Routes>
      </Suspense>
    </div>
  );
}

export default AppLayout;
